import React from 'react';

export default function(props) {
  return (
    <div className="loginWrap">
      <form className="forgotPassword" onSubmit={props.submit}>
        <div className="row title-wrap">
          <h2></h2>
          <h3></h3>
        </div>
        <div className="row">
          <span className="login-icon login-icon-email"></span>
          <input type="email" value={props.formData.email} placeholder=""
                 onChange={props.onChange.bind(null, 'email')} />
        </div>
        <div className="row actions">
          <button type="submit"></button>
        </div>
      </form>
      <div className="notify">
        <span></span>
        <a href="#" className="returnSignnIn" onClick={props.switch.bind(null, 'signIn')}></a>
      </div>
    </div>

    )
}


