import './DeleteDialog.css';
import React from 'react';

export default function(props) {
  return (
    <div className="deleteDialog">
      <div className="main">
        <div className="left">
          <img src="./icon.jpg" alt=""/>
        </div>
        <div className="right">
          <h3> "{props.title}" </h3>
          <p className="desp"> </p>
        </div>
      </div>
      <div className="row actions">
          <div className="btn-group">
            <button className="confirm-btn" onClick={props.onConfirm.bind(this)}></button>
            <button className="cancel-btn" onClick={props.onCancel.bind(this)}></button>
          </div>
      </div>
    </div>
  );
}



