import React from 'react';

export default function(props) {
    return (
      <div className="loginWrap">
        <form className="signUp" onSubmit={props.submit}>
          <div className="row">
            <span className="login-icon login-icon-username"></span>
            <input type="text" value={props.formData.username} placeholder=""
            onChange={props.onChange.bind(null, 'username')} />
          </div>

          <div className="row">
            <span className="login-icon login-icon-password"></span>
            <input type="password" value={props.formData.password} placeholder=""
            onChange={props.onChange.bind(null, 'password')} />
          </div>

          <div className="row">
            <span className="login-icon login-icon-email"></span>
            <input type="email" value={props.formData.email} placeholder=""
                   onChange={props.onChange.bind(null, 'email')} />
          </div>
          <div className="row actions">
            <button type="submit"></button>
          </div>
        </form>
        <div className="notify">
          <span></span>
          <a href="#" className="returnSignnIn" onClick={props.switch.bind(null, 'signIn')}></a>
        </div>
      </div>
    )
}


